<template>
	<view class="toipc-wrap">
		<tab-bar-head :tabBars="tabBars" :tabIndex="tabIndex" @tabtap="toggleTab"></tab-bar-head>
		
		<view class="uni-tab-bar">
			<swiper class="swiper-box" :style="{height: swiperHeight + 'px'}" :current="tabIndex" @change="swiperChange">
				<swiper-item v-for="(item, index) in newsList" :key="index">
					<scroll-view scroll-y="true" class="list" @scrolltolower="loadMore(index)">
						<template v-if="item.list.length">
						<!-- 图文列表 -->
						<view class="toipc-list" v-for="(item1,index1) in item.list" :key="index1">
							<topic-list :item="item1" :index="index1"></topic-list>
						</view>
						
						<!-- 上拉加载 -->
						<load-text :loadText="item.loadText"></load-text>
						</template>
						<template v-else>
						<!-- 无内容 -->
						<no-thing></no-thing>
						</template>
					</scroll-view>
				</swiper-item>
			</swiper>
		</view>
	</view>
</template>

<script>
	import TabBarHead from '../../components/index/swiper-tab-head.vue'
	import LoadText from '../../components/load-more/load-more.vue'
	import NoThing from '../../components/no-thing/no-thing.vue'
	import TopicList from '../../components/news/topic-list.vue'
	export default {
		components:{
			TabBarHead,
			LoadText,
			NoThing,
			TopicList
		},
		data() {
			return {
				swiperHeight: 0,
				tabIndex: 0,
				tabBars: [
					{
						name: "推荐",
						id: 1
					},
					{
						name: "关注",
						id: 2
					},
					{
						name: "本地",
						id: 3
					},
					{
						name: "热点",
						id: 4
					},
					{
						name: "舆论",
						id: 5
					},
					{
						name: "财经",
						id: 6
					},
					{
						name: "搞笑",
						id: 7
					},
					{
						name: "更多",
						id: 8
					}
				],
				newsList: [
					{
						loadText: '上拉加载更多',
						loadTimes: 5, //模拟两次后没有数据
						name: '推荐',
						list: [{
							imageSrc: '../../static/demo/topicpic/1.jpeg',
							title: '我是话题的标题',
							desc: '我是化的描述',
							total: 50,
							dayNum: 10
						},
						{
							imageSrc: '../../static/demo/topicpic/2.jpeg',
							title: '我是话题的标题',
							desc: '我是化的描述',
							total: 150,
							dayNum: 20
						},
						{
							imageSrc: '../../static/demo/topicpic/3.jpeg',
							title: '我是话题的标题',
							desc: '我是化的描述',
							total: 560,
							dayNum: 110
						},
						{
							imageSrc: '../../static/demo/topicpic/4.jpeg',
							title: '我是话题的标题',
							desc: '我是化的描述',
							total: 80,
							dayNum: 20
						},{
							imageSrc: '../../static/demo/topicpic/1.jpeg',
							title: '我是话题的标题',
							desc: '我是化的描述',
							total: 50,
							dayNum: 10
						},
						{
							imageSrc: '../../static/demo/topicpic/2.jpeg',
							title: '我是话题的标题',
							desc: '我是化的描述',
							total: 150,
							dayNum: 20
						},
						{
							imageSrc: '../../static/demo/topicpic/3.jpeg',
							title: '我是话题的标题',
							desc: '我是化的描述',
							total: 560,
							dayNum: 110
						},
						{
							imageSrc: '../../static/demo/topicpic/4.jpeg',
							title: '我是话题的标题',
							desc: '我是化的描述',
							total: 80,
							dayNum: 20
						}]
					},
					{
						list: []
					},
					{
						list: []
					},
					{
						list: []
					},
					{
						list: []
					},
					{
						list: []
					},
					{
						list: []
					},
					{
						list: []
					}
				]
				
			}
		},
		onLoad() {
			uni.getSystemInfo({
				success: (res) => {
					let height = res.windowHeight - uni.upx2px(100)
					this.swiperHeight = height;
				}
			})
		},
		methods: {
			toggleTab(index){
				this.tabIndex = index;
			},
			swiperChange(e){
				// console.log(e.detail)
				this.tabIndex = e.detail.current;
			},
			loadMore(index){//上拉加载
				if(this.newsList[index].loadTimes < 0){
					this.newsList[index].loadText = '没有更多数据了';
					return
				}
				if(this.newsList[index].loadText !== '上拉加载更多' )return
				this.newsList[index].loadText = '正在加载中。。。';
				
				setTimeout(() => {
					let list = this.newsList[index].list;
					
					this.newsList[index].list.push(list[0], list[1])
					// console.log(this.newsList[index].list)
					this.newsList[index].loadText = '上拉加载更多';
					this.newsList[index].loadTimes--;
				},1000) 
				
			}
		}
	}
</script>

<style scoped lang="scss">
	.toipc-wrap{
		.toipc-list{
			padding: 0 20upx;
		}
	}
</style>
